<template>
  <v-container
  id="Pemasukan-&-pengeluaran-sekolah"
  class="fill-height"
  tag="section"
  >
  <v-row>
    <v-col
      v-for="(chart, i) in charts"
      :key="`chart-${i}`"
      cols="24"
      md="6"
    >
      <material-chart-card
        :color="chart.color"
        :data="chart.data"
        :options="chart.options"
        :responsive-options="chart.responsiveOptions"
        :title="chart.title"
        :type="chart.type"
      >
        <template #subtitle>
          <div class="font-weight-light text--secondary">
            <div v-html="chart.subtitle" />
          </div>
        </template>

        <template #actions>
          <v-icon
            class="mr-1"
            small
          >
            mdi-clock-outline
          </v-icon>

          <span
            class="text-caption grey--text font-weight-light"
            v-text="chart.time"
          />
        </template>
      </material-chart-card>
    </v-col>
    <v-col
      cols="12"
      md="12"
    >
      <material-card
        color="primary"
        full-header
      >
        <template #heading>
          <div class="pa-8 white--text">
            <div class="mvp hidden absolute z-30 md:left-1/3 w-screen md:w-1/3">
              <material-card
                color="white"
                full-header
              >
                <template #heading>
                  <div class="flex justify-between mx-4 relative p-6">
                    <h3 class="text-gray-800 text-sm">New +</h3>
                      <v-icon
                        @click="ssclose"
                        color="error"
                        class="hover:opacity-30"
                      >
                        fa-solid fa-rectangle-xmark
                      </v-icon>
                  </div>
                </template>
                <v-card-text>
                  <v-row>
                    <v-col
                      cols="12"
                      md="12"
                    >
                      <v-autocomplete
                        :items="rat"
                        v-model="bulan"
                        label="Bulan*"
                        append-icon="fa-solid fa-moon"
                        multiple
                        filled
                      />
                       <v-text-field
                        v-model="ppengeluaran"
                        label="Pengeluaran*"
                        append-icon="fa-solid fa-calculator"
                        filled
                      />
                       <v-textarea
                        v-model="ddesc"
                        label="Description*"
                        append-icon="fa-solid fa-file-pen"
                        filled
                      />
                    </v-col>
                    <v-col
                    cols="12"
                    md="12"
                    >
                      <v-btn
                        @click="npsave"
                        elevation-2
                        class="uppercase w-full np"
                        color="indigo lighten-2"
                        text
                      >
                        save
                      </v-btn>
                    </v-col>
                  </v-row>
                </v-card-text>
              </material-card>
            </div>
            <div class="g hidden w-screen md:w-1/2 absolute left-0 z-30 md:left-1/4 -translate-x-6 md:-translate-x-10">
              <material-card
                color="accent"
                full-header
              >
                  <template #heading>
                    <div class="w-full">
                      <div class="absolute subheading right-7 -translate-y-4">
                        <v-btn
                          @click="sssclose"
                          color="red"
                          small
                        >
                        <v-icon
                          class="mx-1"
                          small
                        >
                        fa-solid fa-rectangle-xmark
                        </v-icon>
                          Close
                        </v-btn>
                      </div>
                      <v-tabs
                        v-model="tabs"
                        background-color="transparent"
                        slider-color="white"
                        class="pa-2"
                      >
                        <span
                          class="subheading font-weight-light mx-3"
                          style="align-self: center"
                        >Clone from:</span>
                        <v-tab class="mr-3" small>
                          <v-icon class="mr-2">
                            fa-solid fa-cloud-arrow-up
                          </v-icon>
                          Local Storage
                        </v-tab>
                        <v-tab class="mr-3" small>
                          <v-icon class="mr-2">
                            mdi-code-tags
                          </v-icon>
                          URL
                        </v-tab>
                        <v-tab>
                          <v-icon class="mr-2" small>
                            fa-solid fa-server
                          </v-icon>
                          Server
                        </v-tab>
                      </v-tabs>
                    </div>
                  </template>
                  <v-tabs-items
                    v-model="tabs"
                      background-color="transparent"
                    >
                      <v-tab-item
                        :key="1"
                        class="p-5 rounded-xl border border-rose-600 border-dashed"
                      >
                        <v-card-text>
                          <template v-bind="1">
                            <!-- <form action="#" method="POST" enctype="multipart/form-data"> -->
                            <div class="grid">
                              <div class="text-4xl font-semibold text-rose-600 text-center">File Upload :</div>
                              <div class="text-4xl text-rose-600 grid place-items-center mt-5" >
                                <div class="grid" @click="upmath"><button ><i class="fa-brands fa-cloudsmith"></i></button>
                                  <i class="text-xs text-gray-300">Click here(?)</i>
                                  <input type="file" class="hidden" accept="application/json, text/plain" id="m" ></div>
                                  
                                  <button class="v absolute text-xs text-green-500 font-light hidden translate-y-10"><i class="fa-solid fa-file-shield"></i> <span></span></button>
                              </div>
                              <div class="mt-5 flex justify-between mx-9">
                                <button @click="paste" class="py-2 px-4 bg-rose-600 hover:bg-rose-800 save focus:ring-blue-500 focus:ring-offset-blue-200 w-full transition ease-in duration-200 text-white text-center text-base font-semibold shadow-md focus:outline-none focus:ring-2 focus:ring-offset-2  rounded-lg ">Upload</button>
                              </div>
                            </div>
                          </template>
                        </v-card-text>
                      </v-tab-item>
                      <v-tab-item
                        :key="2"
                      >
                      <v-card-text>
                          <template v-bind="1">
                            <v-row
                              align="center"
                              class="flex-nowrap"
                            >
                              <v-col
                                cols="2"
                              >
                              <v-icon
                                class="mx-1"
                                >
                                  mdi-attachment
                                </v-icon>
                              </v-col>
                              <v-col
                                class="font-weight-light"
                                cols="8"
                              >
                              <v-text-field 
                                v-model="grabdom"
                                label="Enter a domain address"
                                @keypress.enter="grap()"
                              />
                              </v-col>

                              <v-col
                                cols="auto"
                                class="text-right"
                              >
                              <v-icon 
                                  class="mx-1"
                                  color="success"
                                  @click="grap()"
                                >
                                  fa-solid fa-check
                                </v-icon>
                              </v-col>
                            </v-row>
                          </template>
                      </v-card-text>
                      </v-tab-item>
                      <v-tab-item
                        :key="3"
                      >
                      <v-card-text>
                          <template v-for="lock in pos">
                            <v-row
                              :key="lock.id"
                              align="center"
                              class="flex-nowrap text-gray-800"
                            >

                              <v-col
                                class="font-weight-light"
                                cols="auto"
                                v-text="lock.id"
                              />
                              <v-col
                                class="font-weight-light"
                                cols="7"
                              >
                                <v-icon
                                class="mx-1"
                                >
                                  mdi-attachment
                                </v-icon>
                                {{ lock.name }}
                              </v-col>
                              <v-col
                                cols="auto"
                                class="text-right"
                              >
                              size: 
                              {{ lock.size }}
                              Bytes
                              </v-col>
                              <v-col
                                cols="auto"
                                class="text-right"
                              >
                                <v-icon 
                                  class="mx-1"
                                  color="success"
                                  @click="link()"
                                >
                                  fa-solid fa-check
                                </v-icon>

                              </v-col>
                            </v-row>
                          </template>
                      </v-card-text>
                      </v-tab-item>
                      </v-tabs-items>
              </material-card>
            </div>
            <div class="text-h4 font-weight-light">
              Statistik pembayaran
            </div>
            <div class="text-caption">
              Last Update on <span>{{timex}}</span>
            </div>
          </div>
        </template>
        <v-card-text>
          <v-data-table
            item-key="name"
            :headers="headers"
            :items="items"
            :search="search"
            :custom-filter="fuck"
          >
          <template v-slot:top>
            <v-row>
            <v-col
              cols="12"
              md="8"
            >
              <v-text-field
                v-model="search"
                label="Search name here..."
                filled
                append-icon="fa-solid fa-magnifying-glass"
                class="mx-1"
              ></v-text-field>
            </v-col>
            <v-col 
              cols="auto"
              md="4"
            >
              <div class=" grid grid-cols-3 mx-1">
                <v-btn
                color="primary"
                class="mx-1"
                @click="nw"
                >
                  <v-icon
                    small
                    class="mx-1"
                  >
                    fa-solid fa-plus
                  </v-icon>
                  <span class="font-bold text-xs">New</span>
                </v-btn>
                  <v-btn
                  color="secondary"
                  class="mx-1"
                  @click="imp"
                >
                  <v-icon
                    small
                    class="mx-1"
                  >
                    fa-solid fa-cloud-arrow-up
                  </v-icon
                  >
                  <span class="font-bold text-xs">Import</span>
                </v-btn>
                <v-btn
                  color="info"
                  class="mx-1"
                  @click="down"
                >
                  <v-icon
                    small
                    class="mx-1"
                  >
                    fa-solid fa-download
                  </v-icon>
                  <span class="font-bold text-xs">Download</span>
                </v-btn>
              </div>
                  
            </v-col>
            </v-row>
          </template>
          <template v-slot:item.action="{ item }">
          <v-icon
            small
            class="mr-2 edit"
            @click="edit(item)"
          >
            mdi-pencil
          </v-icon>
          <v-icon
            small
            class="mr-2 del"
            @click="delet(item)"
          >
            mdi-delete
          </v-icon>
          <v-icon
            small
            class="mr-2 sv"
            @click="save(item)"
          >
          fa-solid fa-floppy-disk
          </v-icon>
          </template>
          </v-data-table>
        </v-card-text>
      </material-card>
    </v-col>
  </v-row>
</v-container>
</template>
<script>
import { get } from 'vuex-pathify'
  import Vue from 'vue'

  const lineSmooth = Vue.chartist.Interpolation.cardinal({
    tension: 0,
  })
export default {
  data: () =>( {
    search: '',
    timex: '',
    charts: [ 
        {
          type: 'Bar',
          color: 'primary',
          title: 'Pengeluaran',
          time: 'updated 4 minutes ago',
          data: {
            labels: ['Jan', 'Feb', 'Mar', 'April', 'Mei', 'Jun', 'Jul', 'Agus', 'Sep', 'Oct', 'Nov', 'Des'],
            series: [
            ],
          },
          options: {
            lineSmooth,
            low: 0,
            high: 2000, 
            chartPadding: {
              top: 0,
              right: 0,
              bottom: 0,
              left: 0,
            },
          },
        },
        {
          type: 'Line',
          color: 'info',
          title: 'Uang pangkal(Dana sisa)',
          time: 'campaign sent 26 minutes ago',
          data: {
            labels: ['Jan', 'Feb', 'Mar', 'April', 'Mei', 'Jun', 'Jul', 'Agus', 'Sep', 'Oct', 'Nov', 'Des'],
            series: [],
          },
          options: {
            lineSmooth,
            low: 0,
            high: 2000, // creative tim: we recommend you to set the high sa the biggest value + something for a better look
            chartPadding: {
              top: 0,
              right: 0,
              bottom: 0,
              left: 0,
            },
          },
        }
      ],
    headers: [
      {
        sortable: false,
        text: 'ID',
        value: 'id',
      },
      {
        sortable: true,
        text: 'Bulan',
        value: 'bulan',
      },
      {
        sortable: false,
        text: 'Pengeluaran',
        value: 'pengeluaran',
      },
      {
        sortable: false,
        text: 'Tanggal_keluar',
        value: 'tglkeluar',
      },
      {
        sortable: false,
        text: 'Dana_sisa',
        value: 'uangkas',
      },
      {
        sortable: false,
        text: 'Dekreipsi',
        value: 'description',
      },
      {
        text: 'Action',
        value: 'action'
      }
    ],
    items: [],
    rat: [],
    tabs: 0,
    tasks: [],
    edit (item){
      this.editedIndex = this.items.indexOf(item)
      this.editedItem = Object.assign({}, item)
      // this.dialog = true
        var x = document.querySelectorAll("tbody")[0].querySelectorAll("tr")
        x[item.id - 1].contentEditable = "true"
    },
    save (item){
        this.editedIndex = this.items.indexOf(item)
        this.editedItem = Object.assign({}, item)
        // this.dialogDelete = true
        // Start collection
        var x = document.querySelectorAll("tbody")[0].querySelectorAll("tr")
          var y = x[item.id - 1].querySelectorAll("td")
          var m = y[1].innerText
          if (this.items[item.id - 1].bulan !== m){
            this.items[item.id - 1].bulan = y[1].innerText
            this.items[item.id - 1].pengeluaran = y[2].innerText
            this.items[item.id - 1].tglkeluar = y[3].innerText
            var c = item.id
          setTimeout(() => {
            x[item.id - 1].contentEditable = "false"
            alert("ID : " + c +" saved")
          }, 300)
          }else if(this.items[item.id - 1].tagihan !== y[3].innerText){
            this.items[item.id - 1].bulan = y[1].innerText
            this.items[item.id - 1].pengeluaran = y[2].innerText
            this.items[item.id - 1].tglkeluar = y[3].innerText
            var c = item.id
          setTimeout(() => {
            x[item.id - 1].contentEditable = "false"
            alert("ID : " + c +" saved")
          }, 300)
          }else{
            x[item.id -1].contentEditable = "false"
          }
    },
    delet (item){
      this.editedIndex = this.items.indexOf(item)
      this.editedItem = Object.assign({}, item)
      // this.dialogDelete = true
      var x = document.querySelectorAll("tbody")[0].querySelectorAll("tr")
      x[item.id - 1].style.display = "none"
    },
  }),
  created(){
      var m = localStorage.getItem("exp")
      if (m !== null) {
        this.items = JSON.parse(m)
        this.items.forEach((item, i) => {
          this.rat[i++] = item.bulan
          this.charts[0].data.series[0] = Array.from({length: this.items.length},(_, i) => this.items[i].pengeluaran)
          this.charts[1].data.series[0] = Array.from({length: this.items.length},(_, i) => this.items[i].uangkas)
        })
      }else{
        var y = [
        {
          id: 1,
          bulan : "Jan"
        },
        {
          id: 2,
          bulan : "Feb"
        },
        {
          id: 3,
          bulan : "Mar"
        },
        {
          id: 4,
          bulan : "April"
        },
        {
          id: 5,
          bulan : "Mei",
        },
        {
          id: 6,
          bulan : "Jun"
        },
        {
          id: 7,
          bulan : "Jul",
        },
        {
          id: 8,
          bulan :  "Ogos"
        },
        {
          id: 9,
          bulan :  "Sep"
        },
        {
          id: 10,
          bulan :  "Oct"
        },
        {
          id: 11,
          bulan :  "Nov"
        },
        {
          id: 12,
          bulan :  "Des"
        },
      ];
      var t = new Date()
      var m = t.getDay()
      var l = 'Lorem ipsum Querans'
      
      this.items = y
      this.items.forEach((item, i) => { 
        this.rat[i] = this.items[i].bulan
        item.tglkeluar = m
        item.pengeluaran = 1200
        item.uangkas = 0
        item.description = l
        if (item.bulan == "Jan") {
          item.pengeluaran = 888
          item.uangkas = 312
          item.tglkeluar = m + i
          item.description = 'Twinigram'
        }else if(item.bulan == "Jul"){
          item.pengeluaran = 0
          item.uangkas = 1200
          item.description = 'Twinigram'
          item.tglkeluar = m + i
        }else if(item.bulan == "Oct"){
          item.pengeluaran = 653
          item.uangkas = 547
          item.tglkeluar = m + i
        }else if(item.bulan == "Nov"){
          item.pengeluaran = 753
          item.uangkas = 447
          item.tglkeluar = m + i
        }else if(item.bulan == "Des"){
          item.pengeluaran = 803
          item.uangkas = 397
          item.tglkeluar = m + i
        }
        this.charts[0].data.series[0] = Array.from({length: this.items.length},(_, i) => this.items[i].pengeluaran)
        this.charts[1].data.series[0] = Array.from({length: this.items.length},(_, i) => this.items[i].uangkas)
      })
      // loreve
      }
      var date = new Date()
     var month = ["January","February","March","April","May","June","July","August","September","October","November","December"]
     this.timex = date.getDate() +'th '+ month[date.getMonth()]+ ', '+ date.getFullYear()
    },
  mounted(){
    
  },
  methods: {
      fuck(value, search, item) {
        return value != null &&
          search != null &&
          typeof value === 'string' &&
          value.toString().toLocaleLowerCase().indexOf(search) !== -1
      },
      nw(){
        const mk = document.querySelectorAll(".mvp")[0]
        if (mk.style.display == "none") {
          mk.style.display = "block"
        } else {
          mk.style.display = "none"
        }  
      },
      imp(){
        const l = document.querySelectorAll('.g')[0]
        if (l.style.display == "none") {
          l.style.display = "block"
        } else {
          l.style.display = "none"
        }
      },
      down(){
        var v = JSON.stringify(this.items)
        var nv = new Blob([v])
        var y = document.createElement("a")
        y.href = window.URL.createObjectURL(nv);
        y.download = "fakes.json"
        y.click()
      },
      upmath(){
        var r = document.getElementById("m") 
        var z = document.querySelectorAll('.v')[0]
        r.click()
        r.addEventListener("input", () => {
          if (r.files.length == 1) {
            z.querySelectorAll("span")[0].innerText = r.files[0].name
            z.style.display = "block"
          }
        })
        
      },
      paste(){
        var x = document.getElementById("m")
        var y = x.files[0]
        var l = []
        var query = [
          {
            'id': this.pos.length + 1,
            'name': y.name,
            'lastModiiedf': y.lastModified,
            'lastModifiedDate': y.lastModifiedDate,
            'size': y.size,
            'type': y.type
          }
        ]
        // Save
        this.pos = this.pos.concat(query)
        var sav = localStorage.setItem("file", JSON.stringify(this.pos))
        if (sav == false){
          alert("err")
        }else{
          alert("Anda tidak bisa mengupload file di versi demo")
        }
        // SET
        // var fromd = new FormData()
        // fromd.append("file", y)
        // fetch('http://localhost:8080/#m[0]file/', {
        //   method: 'post',
        //   body: fromd
        // })
        // .then((response) => response.json())
        // .then((result) => {
        //   console.log('Success:', result);
        // })
        // .catch((error) => {
        //   console.error('Error:', error);
        // });
      },
      grap(){
        let q = []
        const m = this.grabdom
        const d = new Date()
        fetch(m)
        .then(res => res.json())
        .then(json => {
          var y = json.results
          // y.forEach((item, i) => {
          //   var set = [
          //   {
          //     'name': y[i++].name.first + ' ' + y[i++].name.last,
          //     'email': y[i++].email,
          //     'uid': y[i++].uid,
          //     'kelas': y[i++].kelas,
          //   },
          // ]
          // this.data = set
          // })
          const d = new Date()
          var yg = this.pos.length + 1
        
        var query = [
          {
            'id': yg,
            'name': m,
            'lastModiiedf': y.lastModified,
            'lastModifiedDate': d,
            'size': this.pos[0].size * yg,
            'type': "application/json"
          }
        ]
        // Save
        this.pos = this.pos.concat(query)
        })
      },
      ssclose(){
        var mn = document.querySelectorAll(".mvp")[0]
        if(mn.style.display == "none"){
          mn.style.display = "block"
        }else{
          mn.style.display = "none"
        }
      },
      sssclose(){
        var mn = document.querySelectorAll(".g")[0]
        if(mn.style.display == "none"){
          mn.style.display = "block"
        }else{
          mn.style.display = "none"
        }
      },   
      npsave(){
        if(this.bulan.length > 0){
          if ((this.ppengeluaran && this.ddesc) == undefined) {
            alert("Isi bagian yang kosong")
          }else{
          this.bulan.forEach((item, i) => {
           var bad = this.bulan[i]
           var mat = this.items
           function box(ma) {
            if (ma.bulan == bad) {
              return ma.bulan == bad
            }
           }
           var gc = mat.filter(box)
           var tgl = () => {
            var th = new Date()
            if (th.getDate() < 10) {
              return '0'+ th.getDate()
            }else{
              return th.getDate()
            }
           }
           var pg =  Array.from({length: gc.length},(_, i) => gc[i++].pengeluaran = this.ppengeluaran)
           var dc =  Array.from({length: gc.length},(_, i) => gc[i++].description = this.ddesc)           
           var uk =  Array.from({length: gc.length},(_, i) => gc[i++].uangkas = 1200 - gc[0].pengeluaran)        
           var tq =  Array.from({length: gc.length},(_, i) => gc[i++].tglkeluar = tgl())   
          })
          setTimeout(() => {
            localStorage.setItem("exp", JSON.stringify(this.items))
            window.location.reload(true)
          }, 3000)
          }
        }else if(this.bulan == undefined){
          alert("Isi bagian yang kosong")
        }
      },
    },
}
</script>